@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);

@import url(https://fonts.googleapis.com/css?family=Ubuntu);

.container{
  display: flex;
  align-items: center;
  justify-content: flex-start; 
  width: 100%;
  padding: 12px 20px;             
  box-sizing: border-box;
  max-width: 1200px;
  margin: 0 auto;

}
.brand{
    display: flex;
    flex: 0 1 auto;
    justify-content: center;
    margin: 0;
    font-family: 'Ubuntu', sans-serif;
    font-size: 30px;
    font-weight: 900;
    color: white;
    
    font-family: 'Ubuntu', sans-serif;

    

}
.menu{
  display: flex;
  gap: 28px;
  justify-content:center;
  align-items: center;
  margin-left: auto;
  flex: 0 1 auto;



}
.menu a{

    text-decoration: none;
    color: white;
    font-size: 20px; 
    font-weight: 300;
    font-family: 'Roboto Mono', monospace;

}

@media (max-width: 980px){
  .brand{ font-size: 32px; }    
  .menu{ gap: 20px; }
}
@media (max-width: 768px) {
  .container {
    flex-direction: column;
    align-items: center;
    justify-content: center;

  }

  .brand {
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    margin-bottom: 10px;
  }

  .menu {

    flex-direction: row; 
    justify-content: center; 
    align-items: center;
    padding-left: 0;
    margin-left: 0;
    gap: 15px; 
  }

  a {
    padding-left: 0px;
    gap: 3em;
    font-size: 16px;
    margin: 0 auto; 
  }
}