@import url(https://fonts.googleapis.com/css?family=Ubuntu);

.header-container{
    display: flex;
    align-items: center;
    justify-self: flex-start;
    gap: 50px;
 
    color: aliceblue;
    font-family: 'Ubuntu', sans-serif;
}
.header {
  display: flex;
  flex-direction: column;
  align-items: flex-start; 
  text-align: left;        
  color: white;
}



.header h1{
    font-size: 50px ;
    font-weight: 700;
}
.header a{
  text-decoration: none;
  color: rgb(165, 165, 165);
}

.cv-button {
  display: inline-block;
  padding: 10px 30px;
  border: 2px solid rgb(111, 111, 111);
  border-radius: 40px;
  box-shadow: 0 0 10px black;


  transition: background 0.3s;
}

.cv-button:hover {
  background-color: #7b7b7b;
}
.img{
  flex :1;
} 
.img img{
  width: 512px;
  

}
@media (max-width: 768px) {
  .header-container {
    flex-direction: column;
    text-align: center;
    padding: 2em;
   margin: 0 auto;
  }
  .header{
    display: flex;
    flex-direction: column;
    align-items: center;

  }

  .header h1 {
    font-size: 32px;
  }

  .header-container  img{
    width: 330px;
    margin: 0 auto;
    align-items: center;
    
    
  }
}
@media (min-width: 768px) and (max-width: 1024px){
  .header-container{
    flex-direction: row;
    text-align: center;
    margin: 0 auto;

  }
  .header{
    display: flex;
    align-items: flex-start;
  }
  .header h1 {
    font-size: 42px;
  }
    .header h3 {
    font-size: 22px;
  }
    .header a {
    font-size: 22px;
  }
  .header-container img{
    width: 300px;
    height: 300px;
    margin: 0 auto;
    align-items: center;
  }
}
