.construction{
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: center;
    max-width: 1024px;
    width: 100%;
    margin: 1.5em auto;
  

   
    
}
.construction p{
    justify-content: flex-start;
    color: aliceblue;
    font-size: 20px;
    background-color: #242424;
   
}

.construction img{
    width: 30px;
    height: 30px;
    background-color: #242424;
    align-items: center;
    animation: spin 8s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}
@media (max-width: 768px){
    .construction{
       justify-content: center;
    }
}