/* Section spacing: œÄœåœÉŒø Œ±œÄŒ≠œáŒµŒπ Œ±œÄœå œÑŒø banner œÄŒ¨ŒΩœâ Œ∫Œ±Œπ Œ±œÄœå œÑŒ± Skills Œ∫Œ¨œÑœâ */
.pj-wrapper{
  max-width: 1024px;
  width: 100%;
  margin: 2em auto;

}
.projects-section {
  width: 100%;
  max-width: 1200px;
  margin: 48px auto 56px;   
  
}

.projects-section h2 {
  font-size: 26px;
  color: white;
  margin-bottom: 24px;
}

/* === ŒëœÄŒªœéŒΩŒøœÖŒºŒµ œÑŒπœÇ Œ∫Œ¨œÅœÑŒµœÇ ŒºŒµ Grid === */
.pj {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 24px;
}

/* Œó Œ∫Œ¨œÅœÑŒ± Œ≥ŒµŒºŒØŒ∂ŒµŒπ œÑŒø Œ¥ŒπŒ±Œ∏Œ≠œÉŒπŒºŒø œÄŒªŒ¨œÑŒøœÇ œÑŒ∑œÇ œÉœÑŒÆŒªŒ∑œÇ */
.projects-card {
  /* Œ±œÜŒ±ŒπœÅŒøœçŒºŒµ œÑŒ± flex/max-width œéœÉœÑŒµ ŒΩŒ± Œ±œÄŒªœéŒΩŒµœÑŒ±Œπ */
  background: #2a2a2a;
  border-radius: 10px;
  padding: 2px;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0,0,0,.3);
  transition: transform .3s ease;
  height: 100%;
  
}

.projects-card:hover { transform: translateY(-3px); }

.projects-card img {
  width: 100%;
  height: 200px;              /* Œ∫œÅŒ±œÑŒ¨ œÑŒø ŒØŒ¥ŒπŒø œçœàŒøœÇ œåœÄœâœÇ œÜŒ±ŒØŒΩŒµœÑŒ±Œπ œÑœéœÅŒ± */
  object-fit: contain;        /* ‚úÖ Œ¥ŒµŒØœáŒΩŒµŒπ ŒüŒõŒó œÑŒ∑ŒΩ ŒµŒπŒ∫œåŒΩŒ± œáœâœÅŒØœÇ ŒΩŒ± Œ∫œåŒ≤ŒµœÑŒ±Œπ */
  background-color: #1a1a1a;  /* œÄœÅŒøŒ±ŒπœÅŒµœÑŒπŒ∫Œ¨: Œ¥ŒπŒ±Œ∫œÅŒπœÑŒπŒ∫œå œÜœåŒΩœÑŒø Œ≥œçœÅœâ Œ±œÄœå œÑŒ∑ŒΩ ŒµŒπŒ∫œåŒΩŒ± */
  border-radius: 8px;
  display: block;
  margin: 0 auto;
 
}
.projects-card a {
  text-decoration: none;


}

.projects-card h3 { margin-top: 12px; font-size: 18px; color: #fff; text-decoration: none;}
.projects-card p  {text-decoration: none; margin-top: 6px;  font-size: 14px; color: #ddd; }

@media (max-width: 768px)  {
  .projects-section h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }

  .pj {
    grid-template-columns: 1fr;  /* ŒúŒØŒ± œÉœÑŒÆŒªŒ∑ */
    gap: 24px;
  }

  .projects-card {
    margin: 0 auto;
    max-width: 90%;
  }
}

@media (max-width: 480px) {
  .projects-section h2 {
    font-size: 22px;
  }

  .projects-card {
    max-width: 95%;
  }

  .projects-card img {
    height: 180px;
  }
}
/* ============ TABLET VIEW (768px - 1024px) ============ */
@media (min-width: 768px) and (max-width: 1024px) {

  .projects-section {
    max-width: 950px;
    margin: 0 auto;
    padding: 20px 0;
    text-align: center;
  }

  .projects-section h2 {
    font-size: 26px;
    margin-bottom: 24px;
  }



  /* === ŒöŒëŒ°Œ§ŒïŒ£ === */
  .projects-card {
    margin: 0 auto;
    width: 100%;
    max-width: 420px;            /* Œ≥ŒπŒ± ŒπœÉŒøœÅœÅŒøœÄŒ∑ŒºŒ≠ŒΩŒµœÇ Œ∫Œ¨œÅœÑŒµœÇ */
    background-color: #1f1f1f;
    border-radius: 10px;
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }

  .projects-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
  }

  .projects-card img {
    width: 100%;
    height: 200px;               /* œÉœÑŒ±Œ∏ŒµœÅœå œçœàŒøœÇ Œ≥ŒπŒ± Œ∫Œ±Œ∏Œ±œÅœå layout */
    object-fit: cover;
    display: block;
  }

  .projects-card h3 {
    font-size: 18px;
    color: #fff;
    margin: 12px 0 6px;
  }

  .projects-card p {
    font-size: 14px;
    color: #ccc;
    margin-bottom: 16px;
  }
}
